<template lang="pug">
#Home
  .body-container
    div
      img.moto-img(
        src="../assets/moto.jpg",
        alt="A single image",
        width="300px",
        style="filter: hue-rotate(120deg);"
      )
      h3 Francisco Thiago
      small
        a.social.txt-teal(href="https://github.com/fthiagogv")
          i.fa.fa-github.fa-3x
        a.social.txt-teal(href="https://linkedin.com/in/fthiagogv")
          i.fa.fa-linkedin-square.fa-3x
        a.social.txt-teal(href="https://twitter.com/fthiagogv")
          i.fa.fa-twitter-square.fa-3x
    .vl(style="height: 200px")
    div
      a(@click="showProjects")
        p Projects
      a(@click="showThemes")
        p Themes
      a(@click="showAbout")
        p About Me
    .vl(v-if="showContent", style="height: 130px;")
    .content-body(v-if="projects")
      a(target="_blank", href="https://fthiagogv.github.io/ImageToPdf/")
        p ImageToPdf (Converter)
    .content-body(v-if="themes")
      a(target="_blank", href="https://marketplace.visualstudio.com/items?itemName=fthiagogv.NatureRainbow-Theme")
        p Nature Rainbow (VS Code)
      a(target="_blank", href="https://atom.io/themes/naturegreen-dark-syntax")
        p Nature Green Dark (Atom)
      a(target="_blank", href="https://atom.io/themes/naturegreen-light-syntax")
        p Nature Green Light (Atom)
      a(target="_blank", href="https://github.com/fthiagogv/naturepurple-dark-syntax")
        p Nature Purple (Atom)
      a(target="_blank", href="https://atom.io/themes/naturerainbow-dark-syntax")
        p Nature Rainbow (Atom)
    .content-body(v-if="about")
        p I'm a brazilian developer that love what I do.
        p A master degree student on
          =" "
          a(target="_blank", href="https://uece.br/macc")
            u UECE
          | , studying about serverless, microservices, self-adaptive, distributed systems, etc.
        p Currently I'm working with Vuejs and Laravel.

</template>

<script>
export default {
  name: 'Home',
  components: {
  },
  data () {
    return {
      projects: false,
      themes: false,
      about: false
    }
  },
  methods: {
    showProjects () {
      this.offAll()
      this.projects = !this.projects
    },
    showThemes () {
      this.offAll()
      this.themes = !this.themes
    },
    showAbout () {
      this.offAll()
      this.about = !this.about
    },
    offAll () {
      this.projects = false
      this.themes = false
      this.about = false
    }
  },
  computed: {
    showContent: function () {
      return this.projects || this.themes || this.about
    }
  }

}
</script>

<style scoped>
a {
  text-decoration: none;
  color: hsl(150, 40%, 50%);
}
.body-container {
  text-align: center;
  color: hsl(150, 40%, 50%);
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  align-content: center;
  height: 100vh;
}
.vl {
  border-left: 1px solid hsla(150, 50%, 50%, 0.3);
  margin-left: 50px;
  margin-right: 50px;
}
.social {
  margin: 10px;
}
.content-body {
  width: 300px;
  text-align: left;
}
</style>




